.todoApp {
  margin: 10px auto;
  padding: 2px;
  max-width: 100%;
  background-color: rgb(55, 96, 98);
}

@media (min-width: 350px) {
  .todoApp {
    max-width: 95%;
  }
}
@media (min-width: 500px) {
  .todoApp {
    max-width: 70%;
  }
}
@media (min-width: 700px) {
  .todoApp {
    max-width: 55%;
  }
}
@media (min-width: 1100px) {
  .todoApp {
    max-width: 35%;
  }
}
@media (min-width: 1500px) {
  .todoApp {
    max-width: 30%;
  }
}
@media (min-width: 1700px) {
  .todoApp {
    max-width: 25%;
  }
}

.i {
  width: 220px;
  padding: 10px;
  margin: 20px 10px;
  font-size: larger;
  border-style: none;
  border-radius: 6px;
  outline-style: none;
}

@media (max-width: 350px) {
  .i {
    width: 200px;
  }
}

@media (max-width: 300px) {
  .i {
    width: 170px;
  }
}

.b {
  width: 65px;
  padding: 6px;
  font-size: larger;
}

.bd {
  margin: 10px;
  color: antiquewhite;
  background-color: brown;
  padding: 4px;
  font-size: medium;
}

.bdBlock {
  display: flex;
  justify-content: center;
}
