.todoEdit {
  float: left;
  overflow: hidden;
  width: 300px;
  height: 30px;
  background-color: antiquewhite;
  padding: 10px;
  margin: 15px 10px;
  font-size: larger;
  border-style: none;
  border-radius: 6px;
  outline-style: none;
}

.todo {
  overflow: hidden;
  width: 220px;
  height: 30px;
  background-color: rgb(215, 227, 250);
  padding: 2px 10px;
  margin: 10px 10px;
  font-size: larger;
  border-style: none;
  border-radius: 6px;
  outline-style: none;
}

@media (max-width: 350px) {
  .todo {
    width: 200px;
  }
  .todoEdit {
    width: 220px;
  }
}

@media (max-width: 300px) {
  .todo {
    width: 170px;
  }
}

@media (min-width: 800px) {
  .todo {
    width: 300px;
  }
  .todoEdit {
    width: 300px;
  }
}

.check {
  width: 20px;
  height: 20px;
}

.b {
  padding: 5px;
  margin: 10px 0;
  font-size: large;
}

.bd {
  margin: 20px;
  color: antiquewhite;
  background-color: orangered;
  padding: 6px;
  font-size: larger;
}
